
body {
    color: rgba(0, 0, 0, 0.5);
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    position: relative;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55); }
.wrapper {
    margin: 65px 0px; }
.container {
    max-width: 999px;
    padding: 0px 16px;
    margin: auto; }
.header {
    overflow-x: clip;
    padding: 42px 0px 65px 0px;
    position: absolute;
    left: 16px;
    right: 16px;
    top: -160px;
    background: #ffffff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    &__nav {
        padding: 0px 0px; } }
.nav__list {
    display: flex;
    gap: 22px;
    list-style-type: none;
    padding-inline-start: 0px; }
.list__item {
    flex-grow: 1;
    position: relative; }
.list__btn {
    color: rgba(0, 0, 0, 0.5);
    border: none;
    padding: 10px 30px;
    width: 100%;
    background: inherit;
    border-radius: 8px; }
.list__btn:active {
   border: 1px solid #000000; }
.list__btn:hover {
    box-shadow: 0px 0px 0px 2px; }
.item__link_active * {
    color: #FFFFFF;
    background: #000000; }
.list__input {
    top: 0;
    bottom: 0;
    position: absolute;
    opacity: 0; }
.list__img {
    display: block;
    margin: 0 auto;
    height: 40px; }

.nav__btn {
    display: none; }
.nav__btn-menu {
    position: relative;
    color: #000000;
    padding: 15px 0px; }
.nav__btn-menu::after,
.nav__btn-menu::before {
    content: "";
    display: block;
    position: absolute;
    height: 2px;
    border-radius: 3px;
    background: #000000;
    transition: all 0.5s ease-in-out; }
.nav__btn-menu::before {
    box-shadow: 0 -9px 0 0;
    top: 15px;
    width: 25px; }
.nav__btn-menu::after {
    width: 25px;
    bottom: 4px; }


.nav__btn-menu_active::after {
    transform: rotate(50deg);
    bottom: 13px; }

.nav__btn-menu_active::before {
    transform: rotate(-50deg);
    box-shadow: 0 0px 0 0; }



.promo__wrapper {
    text-align: center;
    margin-top: 10pc; }

.promo__title {
    font-family: 'Open Sans', sans-serif;
    line-height: 110px;
    color: #000000;
    font-size: calc(69px + (100vw / 99));
    font-weight: 400; }

.promo__text {
    font-family: 'Rubik';
    font-size: 18px;
    line-height: 28px; }



.content__wrapper_main {
    display: flex;
    flex-wrap: wrap;
    gap: 75px; }

.wrapper__title {
    margin-bottom: 24px; }



.content__wrapper_main > div {
    flex-basis: 324px; }



/* //------------------------------ */


.title {
    font-family: 'Open Sans','Open Sans Hebrew';
    font-weight: 400;
    font-size: 40px;
    line-height: 54px;
    color: #000000; }

.subtitle {
    color: #000000;
    font-family: 'Open Sans','Open Sans Hebrew';
    font-weight: 700;
    font-size: 24px;
    line-height: 33px; }


.content__column-left {
    flex-grow: 1; }



.btn__main {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    padding: 8px 30px;
    border: 1px solid #000000;
    border-radius: 4px;
    background: inherit; }

.video {
    flex-basis: auto;
    height: 200px;
    background: rgb(50, 53, 73); }
.gallery {
    display: flex;
    flex-wrap: wrap-reverse;
    flex-direction: column;
    gap: 24px;
    height: 490px; }
.gallery__item {
    flex-grow: 1;
    flex-shrink: 2;
    flex-basis: 200px;
    border-radius: 8px;
    transition: all 0.3s ease-in-out;
    background: rgb(50, 53, 73); }


.gallery__item:nth-child(1) {
    flex-grow: 0;
    background: rgb(255, 136, 0);
    background: url('../img/image-1.png') no-repeat center/cover; }


.gallery__item:nth-child(2) {
    background: url('../img/image-3.png') no-repeat center/cover; }


.gallery__item:nth-child(3) {
    background: url('../img/image%204.png') no-repeat center/cover; }


.gallery__item:hover {
    box-shadow: inset 0px 1px 120px 20px; }



.box__text {
    max-width: 500px; }

.content__text * + * {
    margin-top: 42px; }

.box__info > *:not(:last-child) {
    margin-bottom: 16px; }

.column-reverse {
    flex-direction: inherit; }

.box {
    border-radius: 8px;
    text-align: left;
    color: #FFFFFF; }

.box .subtitle {
    color: #FFFFFF; }

.box__aside {
    opacity: -0.5;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: rgba(255, 255, 255, 0.5);
    transition: all 0.5s cubic-bezier(0.22, 0.61, 0.36, 1); }

.box:hover .box__aside {
    opacity: 1; }

.box:hover .box__img {
    object-position: 50% 60%;
    transform: scale(1.5); }



.content__wrapper {
    width: auto;
    display: flex;
    flex-wrap: wrap;
    gap: 16px; }



.content__wrapper .box {
    flex: 1 1 200px;
    position: relative;
    overflow: hidden; }

.box__img {
    transition: all 0.9s ease-out;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    height: -webkit-fill-available;
    width: 100%;
    object-position: unset;
    object-fit: none; }

.box__center {
    margin: auto;
    padding: 56px 35px 82px 35px;
    display: table; }

.box__padding {
    padding: 0px 32px;
    padding-top: 110px;
    padding-bottom: 17px; }

.box {
    background: linear-gradient(359.72deg, rgba(0, 0, 0, 0.5) 2.63%, rgba(0, 0, 0, 0) 136.03%); }





@media (max-width:790px) {
    body {
        text-align: center; }

    .content__wrapper_main > div {
        flex-grow: 1;
        flex-basis: auto; }

    .column-reverse {
        flex-direction: column-reverse; }

    .column-reverse *[class*="box"] {
        flex-basis: auto; }

    .box__text {
        max-width: none; }

    .content__text * + * {
        margin-top: 14px; } }


@media (max-width:1000px) {
    .nav__list {
        flex-direction: column;
        padding: 50px;
        position: absolute;
        background: white;
        border-radius: 8px;
        right: -250px; }
    .nav__btn {
        display: block; } }



footer {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    color: rgba(0, 0, 0, 0.2);
    h6 {
        font-size: inherit; } }
.nav__list_open {
    right: 0; }


